<template>
    <div class="container">
      <div class="header">
        <h1 class="center-text">{{ lang.tips17 }}</h1>
      </div>
      <div class="content">
        <div class="drag-drop-field" @drop="handleDrop" @dragover.prevent>
          <div v-for="(image, index) in images" :key="index" class="image-item">
            <img :src="image.url" alt="Image" />
            <button class="delete-button" @click="deleteImage(index)"></button>
          </div>
        </div>
        <input type="file" id="file-input" @change="handleFileSelection" style="display: none" multiple/>
        <label for="file-input" class="file-upload-button">
          {{ lang.tips18 }}
        </label>
        <p class="upload-info">
          {{ lang.tips19 }}
        </p>
        <div class="footer">
          <button class="el-button el-button--default" @click="goBack">{{ lang.tips3 }}</button>
          <button class="el-button el-button--success" style="background:#345CA0;border:none;" @click="upload" :disabled="uploading">
            {{ uploading ? 'Uploading...' : 'Upload' }}
          </button>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        images: [],
        uploading: false,
        uploadProgress: 0,
        uploadTips: [],
        lang: {},
        user: {},
      };
    },
    created() {
      this.lang = this.$lang('board');
      this.user = JSON.parse(localStorage.getItem('designer_user'));
  
      if (this.user.projects.substring(1).length === 0) {
        this.list = [];
        this.loading = false;
        return;
      }
  
      this.getList();
    },
    methods: {
      handleDrop(event) {
        event.preventDefault();
        const files = event.dataTransfer.files;
        this.handleFiles(files);
      },
      handleFileSelection(event) {
        const files = event.target.files;
        this.handleFiles(files);
      },
      handleFiles(files) {
        for (let i = 0; i < files.length; i++) {
          const file = files[i];
          const reader = new FileReader();
          reader.onload = () => {
            const url = reader.result;
            const extension = file.name.split('.').pop().toLowerCase();
            this.images.push({ id: Date.now(), url: url, extension: extension });
          };
          reader.readAsDataURL(file);
        }
      },
      deleteImage(index) {
        this.images.splice(index, 1);
      },
      upload() {
        const fileTypes = ['jpg', 'jpeg', 'png'];
  
        if (this.images.length === 0) {
          this.$message.error('No files to upload');
          return;
        }
  
        this.uploading = true;
        this.uploadProgress = 0;
        this.uploadTips = [];
  
        const formData = new FormData();
  
        for (let i = 0; i < this.images.length; i++) {
          const image = this.images[i];
          const extension = image.extension;
  
          if (fileTypes.includes(extension)) {
            const file = this.dataURLtoFile(image.url, `image_${i + 1}.${extension}`);
            formData.append('file[]', file);
            formData.append('id[]', image.id);
          } else {
            this.$message.error('Unsupported file type');
            this.uploading = false;
            return;
          }
        }
  
        formData.append('createrId', this.user.id);
        formData.append('fileType', 'YOUR_FILE_TYPE'); // Replace with your file type
  
        const xhr = new XMLHttpRequest();
        xhr.upload.onprogress = (e) => {
          this.uploadProgress = parseFloat(((e.loaded / e.total) * 100).toFixed(2));
        };
  
        xhr.open('POST', 'https://simplerevise.com/api/project/uploadPublicMultiple/', true);
        xhr.onreadystatechange = () => {
          if (xhr.status === 200 && xhr.readyState === 4) {
            const data = JSON.parse(xhr.response);
            done();
            // that.setGroup(r);
            that.$message.success('success');
          }
        };
  
        xhr.onerror = () => {
          this.$message.error('System Error: #500');
          this.uploading = false;
        };
  
        xhr.send(formData);
        alert('Upload successful');
        this.$router.go(-1);
      },
      goBack() {
        this.$router.go(-1);
      },
      dataURLtoFile(dataURL, filename) {
        const arr = dataURL.split(',');
        const mime = arr[0].match(/:(.*?);/)[1];
        const bstr = atob(arr[1]);
        const n = bstr.length;
        const u8arr = new Uint8Array(n);
        for (let i = 0; i < n; i++) {
          u8arr[i] = bstr.charCodeAt(i);
        }
        return new File([u8arr], filename, { type: mime });
      },
    },
  };
  </script>
  
  
<style lang="less" scoped>
.container {
    align-items: center;
    justify-content: center;
    padding: 20px;
    box-sizing: border-box;
    overflow-y: auto;
    max-height: 500px;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 20px;
}

.center-text {
    text-align: center;
    flex-grow: 1;
}

.content {
    margin-bottom: 20px;
}

.drag-drop-field {
    border: 2px dashed #ccc;
    padding: 20px;
    margin-bottom: 20px;
    text-align: center;
    height: 300px;
    /* Set the height to 50% */
}

.image-item {
    display: inline-block;
    position: relative;
    margin-right: 10px;
    margin-bottom: 10px;
    width: 20%;
    height: 200px;
    object-fit: cover;
}

.image-item img {
    width: 100%;
    height: 100%;
}

.delete-button {
    position: absolute;
    top: 5px;
    right: 5px;
    background: none;
    border: none;
    color: red;
    cursor: pointer;
    font-size: 12px;
}

.upload-info {
    margin-top: 10px;
    font-size: 14px;
    color: #999;
    text-align: center;
}

.footer {
    display: flex;
    justify-content: center;
    /* Center the buttons horizontally */
    align-items: center;
    /* Center the buttons vertically */
    margin-top: 20px;
    padding: 20px;
}

.footer button {
    margin-left: 10px;
}

.cancel-button,
.upload-button {
    flex: 0 0 auto;
    /* Disable button stretching */
}

.cancel-button {
    margin-right: 10px;
    /* Add margin between buttons */
}

.upload-button {
    margin-left: 10px;
    /* Add margin between buttons */
}

.delete-button {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 20px;
    height: 20px;
    background-color: white;
    border-radius: 50%;
    border: none;
    color: black;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.delete-button:before {
    content: 'x';
}

.file-upload-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    background-color: #ccc;
    color: #333;
    cursor: pointer;
    margin-bottom: 20px;
    text-align: center;
}
</style>
